import React from 'react';

class SignIn extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: ''
        };
    };

    onEmailChange = (event) => {
        this.setState({ email: event.target.value });
    };

    onPasswordChange = (event) => {
        this.setState({ password: event.target.value });
    };

    onFormSubmit = () => {
        const { email, password } = this.state;
        const { handleRoute, loadUser } = this.props;
        fetch('/api/signin', {
            method: 'post',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ email, password })
        })
            .then(res => res.json())
            .then(user => {
                if (user.id) {
                    loadUser(user);
                    handleRoute('home', true);
                };
            });

    };

    render() {

        const { handleRoute } = this.props;

        return (
            <article className="br3 ba b--black-10 mv6 shadow-5 w-100 w-50-m w-25-l mw5 center">
                <main className="pa4 black-80">
                    <div className="measure">
                        <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                            <legend className="f2 fw6 ph0 mh0">Sign In</legend>
                            <div className="mt3">
                                <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                                <input
                                    className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address" id="email-address"
                                    onChange={this.onEmailChange}
                                />
                            </div>
                            <div className="mv3">
                                <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                                <input
                                    className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password" id="password"
                                    onChange={this.onPasswordChange}
                                />
                            </div>
                        </fieldset>
                        <div className="">
                            <input
                                className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib"
                                onClick={this.onFormSubmit}
                                // onClick={() => handleRoute('home', true)}
                                type="submit"
                                value="Sign in" />
                        </div>
                        <div className="lh-copy mt3">
                            <p
                                className="f5 link dim black db pointer"
                                onClick={() => handleRoute('register', false)}
                            >
                                Register
                            </p>
                        </div>
                    </div>
                </main>
            </article>
        )
    };
};

export default SignIn;